  height: 0.96rem;
  background: #141d27;
  position: fixed;
  bottom: 0;
  z-index: 5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.footer .footer-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #141d27;
}
.footer .footer-left .outCart {
  display: inline-block;
  position: absolute;
  top: -0.2rem;
  width: 1.12rem;
  height: 1.12rem;
  border-radius: 50%;
  background: #141d27;
  margin-left: 0.24rem;
  padding: 0.12rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.footer .footer-left .outCart .inCart {
  width: 0.88rem;
  height: 0.88rem;
  line-height: 0.88rem;
  border-radius: 50%;
