  background-color: #2b343c;
}
.footer .footer-left .outCart .inCart.highlight {
  background: #00a0dc;
}
.footer .footer-left .outCart .inCart i {
  font-size: 0.48rem;
  color: rgba(255, 255, 255, 0.4);
}
.footer .footer-left .outCart .inCart i.highlight {
  color: white;
}
.footer .footer-left .outCart .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.48rem;
  height: 0.32rem;
  line-height: 0.37rem;
  text-align: center;
  border-radius: 0.16rem;
  font-size: 0.09rem;
  font-weight: 700;
  color: white;
  background: #f01414;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.footer .footer-left .price {
  display: inline-block;
  font-size: 0.32rem;
  font-weight: 700;
  line-height: 0.48rem;
  color: rgba(255, 255, 255, 0.4);
  margin: 0.24rem 0;
  margin-left: 1.6rem;
  padding-right: 0.24rem;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.footer .footer-left .price.highlight {
