}
.footer .footer-left .desc {
  display: inline-block;
  font-size: 0.30rem;
  font-weight: 300;
  line-height: 0.48rem;
  color: rgba(255, 255, 255, 0.4);
  vertical-align: top;
  margin-top: 0.28rem;
  padding-left: 0.24rem;
}
.footer .footer-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 2.05rem;
          flex: 0 0 2.05rem;
  width: 2.05rem;
}
.footer .footer-right .pay {
  font-size: 0.24rem;
  font-weight: 700;
  line-height: 0.98rem;
  height: 0.96rem;
  color: #26333b;
  background-color: #2b343c;
  color: rgba(255, 255, 255, 0.4);
  text-align: center;
}
.footer .footer-right .pay.not-enough {
  background: #2b333b;
}
.footer .footer-right .pay.enough {
  background: #00b43c;
  color: #fff;
}
.footer .shopcart-list {
  position: absolute;
  bottom: 1rem;
  left: 0;
  z-index: -1;
  width: 100%;
  background: #fff;
}
.footer .shopcart-list .cartheater {
  height: 0.8rem;
  line-height: 0.8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 0.36rem;
  text-align: center;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.footer .shopcart-list .cartheater h1 {
  font-size: 0.28rem;
  font-weight: 200;
  color: #07111b;
}
.footer .shopcart-list .cartheater span {
  font-size: 0.24rem;
  color: #00a0dc;
}
.footer .cart-list {
  padding: 0 0.36rem;
  overflow: hidden;
  max-height: 4rem;
}
.footer .cart-list .food {
  padding: 0.24rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.footer .cart-list .food .name {
  font-size: 0.28rem;
