}
.footer .cart-list .food .price span {
  vertical-align: middle;
  font-size: 0.28rem;
  font-weight: 700;
  color: #f01414;
  line-height: 0.48rem;
  margin-right: 0.24rem;
}
.footer .cart-list .food .price span .cartControl {
  display: inline-block;
  font-size: 0.48rem;
  line-height: 0.48rem;
}
.footer .mask-enter-active,
.footer .mask-leave-active {
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
  opacity: 1;
}
.footer .mask-enter,
.footer .mask-leave-active {
  opacity: 0;
}
.footer .list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
  background: rgba(7, 17, 27, 0.6);
}



