
.fooddetail {
  background-color: white;
  position: fixed;
  top: 0;
  z-index: 999;
  left: 0;
  right: 0;
  bottom: 0;
}
.fooddetail .detail-content {
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.fooddetail .detail-content .image-header {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
}
.fooddetail .detail-content .image-header img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.fooddetail .detail-content .leftIcon {
  font-size: 0.4rem;
  position: fixed;
  top: 0.2rem;
  left: 0.1rem;
  color: white;
}
.fooddetail .info {
  position: relative;
  padding: 0.36rem;
}
.fooddetail .info .name {
  font-size: 0.28rem;
  line-height: 0.28rem;
  font-weight: 700;
  color: #07111b;
  margin-bottom: 0.16rem;
}
.fooddetail .info .desc {
  margin-bottom: 0.36rem;
}
.fooddetail .info .desc span {
  font-size: 0.2rem;
  line-height: 0.2rem;
  color: #93999f;
  margin-right: 0.24rem;
}
.fooddetail .info .price {
  display: inline-block;
  font-size: 0.28rem;
  font-weight: 700;
  color: #f01414;
  line-height: 0.48rem;
}
.fooddetail .info .price .unit {
  font-size: 0.2rem;
  font-weight: normal;
  color: #f01414;
  line-height: 0.48rem;
}
.fooddetail .info .shopCart {
  display: inline-block;
  position: absolute;
  right: 0.36rem;
}
.fooddetail .info .shopCart .text {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  line-height: 24px;
  color: white;
  font-size: 10px;
  padding: 0 12px;
  border-radius: 12px;
  background: #00a0dc;
}
.fooddetail .info .cartcontrol-warp {
  position: absolute;
  right: 0.4rem;
  bottom: 0.4rem;
}
.fooddetail .line {
  width: 100%;
  height: 0.32rem;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.fooddetail .shop {
  padding: 0.36rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.fooddetail .shop .title {
  font-size: 0.28rem;
  line-height: 0.28rem;
  font-weight: 500;
  color: #07111b;
  margin-bottom: 0.16rem;
}
.fooddetail .shop .foodInfo {
  padding: 0 0.16rem;
  font-size: 0.24rem;
  font-weight: 200;
  color: #4d555d;
  line-height: 0.48rem;
}
.fooddetail .ratings-warp {
  padding: 0 0.36rem;
}
.fooddetail .ratings-warp .rating-item {
  padding: 0.32rem 0;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.fooddetail .ratings-warp .rating-item .user {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 0.12rem;
}
.fooddetail .ratings-warp .rating-item .user .time {
  font-size: 0.2rem;
  color: #93999f;
  line-height: 0.48rem;
}
.fooddetail .ratings-warp .rating-item .user .name {
  font-size: 0.2rem;
  vertical-align: top;
}
.fooddetail .ratings-warp .rating-item .user img {
  vertical-align: top;
  height: 0.24rem;
  widows: 0.24rem;
  border-radius: 50%;
}
.fooddetail .ratings-warp .rating-item .comment .icon-damuzhion {
  font-size: 0.24rem;
  color: #00a0dc;
}
.fooddetail .ratings-warp .rating-item .comment .icon-down {
  font-size: 0.24rem;
  color: #93999f;
}
.fooddetail .ratings-warp .rating-item .comment .text {
  font-size: 0.24rem;
  color: #07111b;
  line-height: 0.32rem;
}



