
.detail {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(7, 17, 27, 0.8);
  z-index: 999;
}
.detail .detail-box {
  margin-top: 1.28rem;
  padding-bottom: 0.64rem;
}
.detail .detail-box .name {
  font-size: 0.32rem;
  font-weight: 700;
  color: #fff;
  line-height: 0.32rem;
  text-align: center;
  margin-bottom: 0.32rem;
}
.detail .detail-box .star {
  text-align: center;
  margin-bottom: 0.56rem;
}
.detail .detail-box .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 0 0.72rem;
  margin-bottom: 0.48rem;
  color: #fff;
}
.detail .detail-box .title .line {
  position: relative;
  top: 0.1rem;
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 0.01rem;
  background: rgba(255, 255, 255, 0.2);
  margin: 0 auto;
}
.detail .detail-box .title .text {
  padding: 0 0.24rem;
  font-size: 0.3rem;
  font-weight: 700;
  vertical-align: middle;
}
.detail .detail-box .supports {
  padding: 0 0.96rem;
  margin-bottom: 0.56rem;
}
.detail .detail-box .supports .supports-item {
  color: #fff;
  padding: 0 0 0.24rem 0;
}
.detail .detail-box .supports .supports-item .text {
  vertical-align: middle;
  font-size: 0.24rem;
  font-weight: 200;
  line-height: 0.24rem;
}
.detail .detail-box .supports .supports-item .icon {
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  background-repeat: no-repeat;
  vertical-align: middle;
}
.detail .detail-box .supports .supports-item .icon.decrease {
  background: url(../../../static/imgs/decrease.png);
  background-size: cover;
}
.detail .detail-box .supports .supports-item .icon.discount {
  background: url(../../../static/imgs/discount.png);
  background-size: cover;
}
.detail .detail-box .supports .supports-item .icon.special {
  background: url(../../../static/imgs/special.png);
  background-size: cover;
}
.detail .detail-box .supports .supports-item .icon.invoice {
  background: url(../../../static/imgs/invoice.png);
  background-size: cover;
}
.detail .detail-box .supports .supports-item .icon.guarantee {
  background: url(../../../static/imgs/guarantee.png);
  background-size: cover;
}
.detail .detail-box .bulletin {
  font-size: 0.24rem;
  font-weight: 200;
  color: #fff;
  line-height: 0.48rem;
  padding: 0 0.96rem;
}
.detail .detail-box .close {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.32rem;
  position: absolute;
  bottom: 0.64rem;
  left: 50%;
}



