
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.28rem;
  width: 100%;
  color: #f01414;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab .tab-item > a {
  color: #4d555d;
  display: block;
}
.tab .tab-item > a.router-link-active {
  color: #f01414;
}



