
.cartControl .move-enter-active,
.cartControl .move-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.cartControl .move-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translate(30px) rotate(90deg);
          transform: translate(30px) rotate(90deg);
}
.cartControl .move-enter,
.cartControl .move-leave-active {
  -webkit-transform: translate(20px) rotate(90deg);
          transform: translate(20px) rotate(90deg);
  opacity: 0;
}
.cartControl .add {
  display: inline-block;
  width: 0.36rem;
  line-height: 0.36rem;
  vertical-align: middle;
}
.cartControl .add i {
  font-size: 0.36rem;
  line-height: 0.36rem;
  color: #00a0dc;
}
.cartControl .count {
  padding: 0 0.12rem;
  font-size: 0.2rem;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  color: gray;
  width: 0.24rem;
  line-height: 0.36rem;
}
.cartControl .reduce {
  display: inline-block;
  width: 0.36rem;
  vertical-align: middle;
  line-height: 0.36rem;
}
.cartControl .reduce i {
  font-size: 0.36rem;
  line-height: 0.36rem;
  color: #00a0dc;
}



